// @flow
import {
  Button,
  Flex,
  Heading,
  Image,
  Spinner,
  Stack,
  Text,
  useToast,
} from "@chakra-ui/react";
import Images_Icons from "../../../constants/icons-images";
import { Suspense, useState } from "react";
import TopIconBar from "../../../components/TopIconBar";
import { useNavigate } from "react-router-dom";
import { useStyles } from "../Login/Login";
import MeProtocolInput from "../../../components/MeProtocolInput";
// @ts-ignore
import OtpTimer from "otp-timer";
import { useDispatch } from "react-redux";
import { Dispatch } from "redux";
import { RegisterFormikValues } from "../../../interfaces";
import { resetErrorMsg } from "../../../redux/AuthSlice";
import { GLOBAL_PREFIX, ResetPasswordSchema } from "../../../constants";
import { Formik, FormikHelpers, FormikValues } from "formik";
import MeProtocolFormikButton from "../../../components/MeProtocolFormikButton";
import axios from "axios";
import { BASE_URL } from "../../../config";

export const PasswordResetLoading = () => {
  let navigate = useNavigate();
  const dispatch = useDispatch<Dispatch>();
  const toast = useToast();

  // const {user_data} = useSelector((state: RootState) => state.userReducer)
  const {
    root,
    boxLeft,
    boxLogin,
    leftImg,
    loginColumn,
    head,
    desc,
    basedText,
  } = useStyles();

  const [country, setCountry] = useState<string>("566");
  const submit = () => {
    console.log("button clicked");
  };

  const [show, setShow] = useState<boolean>(false);
  const [isLoading, setIsLoading] = useState<boolean>(false);

  const handleClick = () => setShow(!show);

  // useEffect(() => {
  //     if (window.location.href.indexOf('token=') > -1) {
  //         console.log(window.location.href.substring(window.location.href.indexOf('=') + 1), "ooooo")
  //     }
  // }, [])

  const onSubmit = async (
    { password }: FormikValues,
    actions: FormikHelpers<RegisterFormikValues>
  ) => {
    // console.log( window.location.href.substring(window.location.href.indexOf('=') + 1), password)
    setIsLoading(true);
    try {
      const { data }: any = await axios.post(
        `${BASE_URL}/users/password/reset-by-email`,
        {
          token: window.location.href.substring(
            window.location.href.indexOf("=") + 1
          ),
          newPassword: password,
        }
      );

      // console.log(data, "TTYTYT")
      if (data?.isReset) {
        navigate(`${GLOBAL_PREFIX}/login`);
        toast({
          position: "top",
          title: "Password changed",
          description:
            "Password updated successfully, please login with your new password",
          status: "success",
          duration: 9000,
          isClosable: true,
        });
      }
    } catch (e) {
      console.log(e, "ChangePwdErr");
    } finally {
      setIsLoading(false);
    }
  };

  // @ts-ignore
  return (
    <>
      <TopIconBar />
      <Flex {...root}>
        <Flex {...boxLeft}>
          <Image src={Images_Icons.AuthImage} alt="Me Protocol" {...leftImg} />
        </Flex>
        <Flex {...boxLogin}>
          <Stack {...loginColumn}>
            {window.location.href.indexOf("token=") > -1 ? (
              <>
                <Formik
                  initialValues={{
                    password: "",
                    confirm: "",
                  }}
                  validationSchema={ResetPasswordSchema}
                  // @ts-ignore
                  onSubmit={onSubmit}
                >
                  {(formik) => (
                    <Stack
                      as={"form"}
                      onChange={() => dispatch(resetErrorMsg())}
                      //@ts-ignore
                      onSubmit={formik.handleSubmit}
                      w={{ base: "100%", md: "35vw" }}
                    >
                      <Heading {...head} pb={2}>
                        Create new password
                      </Heading>
                      <Text {...desc}>
                        Please provide and confirm a new password
                      </Text>

                      <Suspense fallback={<Spinner size="sm" />}>
                        <MeProtocolInput
                          name={"password"}
                          type={show ? "text" : "password"}
                          placeholder={"Password"}
                        />
                        <MeProtocolInput
                          name={"confirm"}
                          type={show ? "text" : "password"}
                          placeholder={"Confirm Password"}
                        />
                      </Suspense>
                      <Text
                        as={Button}
                        onClick={handleClick}
                        alignSelf={"flex-end"}
                        {...basedText}
                        pt={"0px"}
                        bg={"transparent"}
                        _hover={{ bg: "transparent" }}
                      >
                        {!show ? "Show" : "Hide"}
                      </Text>

                      <Suspense fallback={<Spinner size="sm" />}>
                        <MeProtocolFormikButton
                          isLoading={isLoading}
                          name={"Change password"}
                          // onClick={onSubmit}
                        />
                      </Suspense>
                    </Stack>
                  )}
                </Formik>
              </>
            ) : (
              <>
                <Heading {...head} pb={2}>
                  Reset password
                </Heading>
                <Text {...desc}>
                  An email containing a password reset link has been sent to
                  your email, please go to your email and click on the link
                  change your password.
                </Text>
                <Text flexDir={"row"} {...desc} textDecoration={"underline"}>
                  My link didn't arrive? Resend link in{" "}
                  <OtpTimer
                    minutes={1}
                    seconds={1}
                    text=" "
                    ButtonText="Resend"
                    resend={submit}
                    background={"black"}
                  />
                </Text>
              </>
            )}
            {/*<MeProtocolButton isDisabled={true} name={"Resend"}/>*/}
          </Stack>
        </Flex>
      </Flex>
    </>
  );
};

export default PasswordResetLoading;
